.botton_content{
      width: 100%;
      height: auto;
      position: relative;
      transform: translateY(-55px);
      z-index: 10;
}

.container.botton{
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 3rem;
}

.botton_card{
      width: 168px;
      height: 110px;
      background: var(--light);
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      border-radius: .4rem;
}

.botton_card img{
      width: 50px;
}

.botton_card strong{
      text-transform: uppercase;
      color: var(--purple-300);
      font-size: .9rem;
      margin-top: .5rem;
}