@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700&family=Zilla+Slab:wght@400;500;600;700&display=swap');

*{
  margIn: 0;
  padding: 0;
  box-sizing: border-box;
}

:root{
  /*  fonts */
  --ft-1: 'Roboto', sans-serif;
  --ft-2: 'Zilla Slab', serif;

  /*  colors */
  --purple-500: #2d2d2d;
  --purple-400: #463a4f;
  --purple-300: #614ca7;
  --purple-200: #7b5acc;
  --purple-100: #ccc7d9;
  --green: #2fbe67;
  --light: #fff;
  --bg: #f2f2f2;
  --border: #f0eef5;
  --footer: #1a1849;


  /* border radius */
  --br-small: .5rem;
  --br: 1rem;

  --p-section: 5rem;

}

body{
  font-size: 62.5%;
  color: var(--purple-500);
  font-family: var(--ft-1);
  min-height: 100vh;
  background: var(--bg);
}

:before,
:after{
  content: '';
  position: absolute;
}

a, span{
  display: inline-block;
}

a{
  text-decoration: none;
}

ul{
  list-style: none;
}

img{
  width: 100%;
  object-fit: cover;
}

button{
  outline: none;
  border: none;
  cursor: pointer;
}

table{
  border-collapse: collapse;
}

.container{
  width: 96%;
  max-width: 1064px;
  margin: 0 auto;
  height: auto;
  position: relative;
}