.header_content{
      width: 100%;
      height: 100vh;
      max-height: 770px;
      background: var(--purple-200);
      position: relative;
}

.header_banner{
      width: 100%;
      height: 100%;
      position: absolute;
      overflow: hidden;
      top: 0;
      left: 0;
}

.header_banner img{
      width: auto;
      min-width: 100%;
      height: 100%;
      filter: grayscale(0);
}

.header_banner:after{
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, .88);
      top: 0;
      left: 0;
      z-index: 2;
}

.container.header{
      z-index: 10;
      height: 100%;
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 3rem;
}

.header_col{
      display: flex;
      align-items: center;
}

.header_col.left{
      color: var(--light);
      flex-direction: column;
      align-items: flex-start;
      justify-content: center;
}

.header_col h1{
      font-family: var(--ft-2);
      font-size: 4rem;
      font-weight: 600;
}

.header_col p{
      font-size: 1rem;
      margin-top: 1rem;
}

.header_col.left a{
      background: var(--purple-200);
      padding: 1rem 2.5rem;;
      border-radius: .2rem;
      color: var(--light);
      text-transform: uppercase;
      font-size: .85rem;
      margin-top: 1rem;
      letter-spacing: 2px;
}

.header_col.right{
      justify-content: center;
}

.header_user{
      display: flex;
      flex-direction: column;
      align-items: center;
      padding: 4rem 5rem;
      background: var(--light);
      border-radius: .5rem;
}

.user_avatar{
      width: 120px;
      height: 120px;
      border-radius: 50%;
      background: var(--purple-100);
      box-shadow: 0 0 0 10px rgba(204, 199, 217, .2);
      overflow: hidden;
      display: flex;
      align-items: center;
      justify-content: center;
}

.header_user strong{
      color: var(--purple-300);
      font-size: 1.3rem;
      margin-top: 1rem;
}

.user_details{
      width: 200px;
      height: 46px;
      display: flex;
      margin-top: 1rem;
      border-radius: .4rem;
      border: solid 1px var(--border);
      padding: 0 1rem;
      align-items: center;
      gap: .5rem;
      color: var(--green);
      font-size: 1.4rem;
}

.user_details span{
      font-size: 1rem;
      color: var(--purple-500);
}

.user_social{
      width: 100%;
      height: 38px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-top: 1.5rem;
}

.user_social a{
      width: 38px;
      height: 38px;
      background: var(--purple-200);
      border-radius: .2rem;
      color: var(--light);
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 1rem;
      box-shadow: 5px 5px 10px rgba(0,0,0,.15);
}