.main_table{
      width: 100%;
      max-width: 830px;
      margin: 0 auto;
      padding: var(--p-section);
}

.main_table table{
      width: 100%;
}

.main_table table tr{
      height: 51px;
}

.main_table table thead tr,
.main_table table tbody tr{
      border-bottom: solid 1px var(--purple-100);
}

.main_table table tr th,
.main_table table tr td{
      text-align: center;
}

.main_table table thead th{
      text-transform: uppercase;
      font-size: .8rem;
}

.main_table table tbody td{
      font-size: .85rem;
}

.main_table table tbody td:first-child{
      color: var(--purple-300);
}

.main_table table tbody td:last-child{
      color: var(--green);
}

.main_table table tbody td img{
      width: 36px;
}

.main_ids__content{
      background: #f8f8f8;
}

.main_ids__content,
.main_powers__content{
      padding-top: var(--p-section);
}